<template>
    <div id="blogs">

        <div class="blogs-left animate__animated animate__zoomIn" >
            <div class="content" v-for="(item,index) in smallBlogs" :key="index" ><!--文章-->
                <img src="./01.png" width="200" height="200">
                <div class="text">
                    <h1  @click="jump(item.id)">{{item.title}}</h1>
                    <div>{{item.brief}}</div>
                    <div class="tape">{{item.type}}</div>
                </div>
            </div>
            <!--分页器组件-->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5, 10, 20, 40]"
                    :page-size="pagesize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="blogs.length"
                    v-show="flag">
            </el-pagination>

        </div>

        <div class="blogs-right">
            <follow :blogs="blogs"></follow><!--右边部分的组件-->
        </div>

    </div>

</template>


<script>
    import follow from '../../components/follow.vue'


    export default {
        name: 'blogs',

        components: {
            "follow": follow,
        },
        methods: {
            jump(index) {
                this.$router.push({path: `/blogs/${index}`});
            },
            handleSizeChange(size) {
                this.pagesize = size;
            },
            handleCurrentChange(currentPage) {
                this.currentPage = currentPage;
            },
        },
        data() {
            return {
                flag: true,
                currentPage: 1, //初始页
                pagesize: 10, // 每页的数据
                total: 0, // 总共的条数
                blogs: []
            }
        },

        computed: {
            smallBlogs() {
                let span = [];
                span = this.blogs.slice((this.currentPage - 1) * this.pagesize, this.currentPage * this.pagesize);
                return span
            },
        },
        created(){
            this.$http('/blogs', 'post', {}).then((res) => {
                this.blogs = res.data.data;
            });
            window.scrollTo(0,0);
        }
    }
</script>

<style lang="scss" scoped>
    #blogs {
        width: 60%;
        margin: 0 auto;
        box-shadow: 0 0 15px 5px #888;
        display: flex;
        flex-wrap: wrap;
        background: #fafafa;
        padding: 20px;
        .blogs-left {
            width: 80%;
            .content {
                display: flex;
                align-items: center;
                box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
                position: relative;
                font-size: 16px;
                margin: 20px;
                transition: all 1s;
                &.content:hover {
                    box-shadow: 0 0 15px 5px #888;
                    transform: scale(1.01, 1.01);
                }
                .blogs-text {
                    width: 70%;
                }
                img {
                    margin-right: 10px;
                }
                .text {
                    h1 {
                        position: absolute;
                        top: 20px;
                        transition: all .5s;
                        padding-bottom: 2px;
                    }
                    h1:hover {
                        color: #3499ff;
                        border-bottom: 1px solid gold;
                    }
                    .tape {
                        position: absolute;
                        bottom: 10px;
                        width: 50px;
                        height: 30px;
                        color: greenyellow;
                        background: #f4fffc;
                        text-align: center;
                        line-height: 30px;
                        font-size: 20px;

                    }

                }
            }
        }

        .blogs-right {
            width: 20%;
            margin-top: 20px;

        }
    }


</style>