<template>

    <div id="app">
        <header>
            <heade :page="page"></heade><!--头部组件-->
        </header>

        <transition name="ani">
            <nav class="app-nav" v-show="searchFixed"><!--导航栏-->
                <ul>
                    <li>
                        <div class="z"><img src="./assets/zz.png" width="200" height="200"></div>
                    </li>
                    <li>
                        <router-link to="/page" class="navsSpan">首页</router-link>
                    </li>
                    <li>
                        <router-link to="/blogs" class="navsSpan">博客</router-link>
                    </li>
                    <li>
                        <router-link to="/messages" class="navsSpan">留言</router-link>
                    </li>
                    <li>
                        <router-link to="/diary" class="navsSpan">日记</router-link>
                    </li>
                    <li>
                        <router-link to="/interlinkage" class="navsSpan">友情链接</router-link>
                    </li>
                    <li>
                        <router-link to="/aboutme" class="navsSpan">关于我</router-link>
                    </li>
                </ul>
            </nav>
        </transition>

        <router-view :page="page" :diary="diary" :aboutme="aboutme" :messages="messages"
                     :interlinkage="interlinkage" :blogs="blogs"></router-view>

        <div data-tname="WaveItem"><!--鼠标点击出现波纹效果-->
            <div class="main-container">
                <div class="waves">
                    <div class="wave" v-for="(item, key) in waves" :key="key" :style="item">
                        <div
                            v-for="n in wavesConfig.total"
                            :key="n"
                            class="wave-item"
                            :style="{
                                transform: `scale(${0.1 * Math.sqrt(n - 1)})`, // 使得波纹大小指数增长
                                opacity: 0.3 * (1 / n), // 因为相互层叠的波纹透明度会相互叠加，需要越小的波纹透明度越低，以免中心颜色过重
                                animationDelay: `${(n - 1) * 0.12}s`, // 越大的波纹越晚出现，以呈现波纹逐渐扩散的效果
                                animationDuration: `${0.6 +n * 0.3 +parseInt(item.width) * 0.002}s`, // 波纹动画时间渐增，表现波纹向外扩散渐慢的效果,波纹尺寸越大动画时间越长。
                                backgroundColor: wavesConfig.waveColor
                                }">
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!--   <a href="http://192.168.8.28:8081/" class="backstage">&lt;!&ndash;去后台&ndash;&gt;
               <i class="iconfont icon-zhandouji"></i>
           </a>-->
        <back></back>
        <footer><!--尾部组件-->
            <foot></foot>
        </footer>

    </div>
</template>


<script>
    import heade from './components/v-head.vue'
    import foot from './components/foot.vue'
    import back from './components/back.vue'


    export default {
        props: ['http'],
        data() {
            return {
                page: {},
                diary: [],
                messages: {},
                aboutme: {},
                interlinkage: {},
                blogs: [],

                waves: [],
                wavesConfig: {
                    maxSize: 100, // px，波纹最大尺寸
                    minSize: 50, // px，波纹最小尺寸
                    zIndexCount: 999, // 波纹父元素其z-index数值
                    waveColor: "#3E8CE3", //波纹基础颜色
                    total: 3 //波纹圈层数
                },
                clear: {
                    delay: 5000,
                    timeoutId: null
                },
                searchFixed: true,
                i: 0
            }
        },
        created() {
            this.$http('/page', 'post', {}).then((res) => {
                this.page = res.data.data;
            });
            this.$http('/diary', 'post', {}).then((res) => {
                this.diary = res.data.data;
            });
            this.$http('/blogs', 'post', {}).then((res) => {
                this.blogs = res.data.data;
            });
            this.$http('/aboutme', 'post', {}).then((res) => {
                this.aboutme = res.data.data;
            });
            this.$http('/interlinkage', 'post', {}).then((res) => {
                this.interlinkage = res.data.data;
            });
            this.$http('/messages', 'post', {}).then((res) => {
                this.messages = res.data.data;
            });
        },
        components: {
            "heade": heade,
            "foot": foot,
            "back": back
        },
        mounted() {
            document.getElementById("app").onclick = e => {
                this.createWave(e);
                this.intervalClearWave();
            };
            window.addEventListener('scroll', this.handleScroll) //window添加一个滚动监听事件，
        },
        methods: {
            createWave(e) {
                // 让新生成的波纹始终在之前波纹的上层产生叠加效果
                if (this.wavesConfig.zIndexCount > 99999) {
                    this.wavesConfig.zIndexCount = 999;
                } else {
                    this.wavesConfig.zIndexCount++;
                }
                // 在一定范围内随机生成波纹的大小
                const waveSize = parseInt(
                    Math.random() * (this.wavesConfig.maxSize - this.wavesConfig.minSize) +
                    this.wavesConfig.minSize
                );
                //添加新的波纹数据
                this.waves.push({
                    left: `${e.clientX - waveSize / 2}px`,
                    top: `${e.clientY - waveSize / 2}px`,
                    zIndex: this.wavesConfig.zIndexCount,
                    width: `${waveSize}px`,
                    height: `${waveSize}px`
                });
            },
            intervalClearWave() {
                clearTimeout(this.clear.timeoutId);
                this.clear.timeoutId = setTimeout(() => {
                    this.waves = [];
                }, this.clear.delay);
            },

            handleScroll() {
                // 页面滚动距顶部距离
                let scrollTop = window.pageYOffset || document.documentElement.scrollTop ||
                    document.body.scrollTop;

                let scroll = scrollTop - this.i;//当前的页面高度
                this.i = scrollTop;//
                if (scroll < 0) {
                    //往下
                    this.searchFixed=true;
                } else {
                    //往上
                    this.searchFixed=false;
                }
            },
            destroyed() {
                window.removeEventListener('scroll', this.handleScroll) //销毁滚动监听事件
            },
        }
    }
</script>

