<template>
    <div id="page">
        <div class="page-left ">
            <div class="page-time animate__animated animate__zoomIn"> <!-- 最上面的时间-->
                <i class="iconfont icon-dengta"></i> 欢迎你在
                <div class="time ">{{date}}</div><!--输出的是浏览器默认的时间格式-->
                访问我的博客！
            </div>
            <div class="rotation-chart animate__animated animate__zoomIn"><!--走马灯-->
                <div class="go">
                    <el-carousel :interval="3000" arrow="arrow" height="300px">
                        <el-carousel-item v-for="item in page.pics" :key="item">
                            <img :src="item">
                        </el-carousel-item>
                    </el-carousel>
                </div>
                <div class="small-img"><!--走马灯右边的图片-->
                    <div class="small-one" v-for="item in page.small" :key="item">
                        <img :src="item">
                    </div>
                </div>
            </div>
            <div class="page-sut animate__animated animate__zoomIn">
                <div class="title">
                    <h2>特别推荐</h2>
                </div>
                <div class="text" v-for="(item,index) in blogs" :key="index" @click="jump(item.id)">
                    <div >
                        <h2>{{item.title}}</h2>
                        <div class="span">
                            <div class="one">
                                <img src="./03.png" alt="">
                            </div>
                            <div class="two">{{item.brief}}</div>
                        </div>
                    </div>
                </div>
            </div>
            <!--<div class="page-content animate__animated animate__zoomIn">&lt;!&ndash;文章部分&ndash;&gt;
                <div class="title">
                    <h2>最新文章</h2>
                </div>

                <div class="content" v-for="(item,index) in blogs" :key="index">
                    <img src="../blogs/01.png" width="200" height="200">
                    <div class="text">
                        <a href="http://192.168.8.28:8080/blogs"><h1>{{item.title}}</h1></a>

                        <div>{{item.brief}}</div>
                        &lt;!&ndash;<div class="tape">{{item.type}}</div>&ndash;&gt;
                    </div>
                </div>
            </div>-->
        </div>

        <div class="paga-right">
            <follow></follow> <!--右部分的组件-->
        </div>
    </div>

</template>


<script>
    import follow from '../../components/follow.vue'

    export default {
        name: 'page',
        props: ['page'],
        components: {
            "follow": follow,
        },
        data() {
            return {
                date: new Date(),  //当前日期
                value2: 0,
                visible: false,
                blogs: [],
            }
        },
        mounted() {
            let _this = this;
            this.timer = setInterval(function () {
                _this.date = new Date().toLocaleString();
                /*toLocaleString() 方法可根据本地时间把 Date 对象转换为字符串，并返回结果。*/
            });
        },
        computed: {},
        beforeDestroy: function () {
            if (this.timer) {
                clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
            }
        },
        created() {
            this.$http('/blogs', 'post', {}).then((res) => {
                this.blogs = res.data.data;
            });
            window.scrollTo(0, 0);
        },
        methods:{
            jump(index) {
                this.$router.push({path: `/blogs/${index}`});
            }
        }
    }
</script>

<style lang="scss" scoped>

    #page {
        width: 60%;
        margin: 0 auto;
        box-shadow: 0 0 15px 5px #888;
        padding: 20px;
        background: #fafafa;
        z-index: 10000;
        display: flex;
        flex-wrap: wrap;
        .page-left {
            width: 80%;
            .page-time {
                font-size: 20px;
                color: #ffc050;
                margin: 10px 0;
                i {
                    font-size: 20px;
                    color: orangered;
                }
                .time {
                    display: inline-block;
                    color: orangered;
                }
            }
            .rotation-chart {
                width: 100%;
                overflow: hidden;
                display: flex;
                .go {
                    display: inline-block;
                    width: 600px;
                    flex: 0 0 600px;
                    margin-right: 10px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                    .el-carousel__item h3 {
                        color: #475669;
                        font-size: 18px;
                        opacity: 0.75;
                        line-height: 300px;
                        margin: 0;
                    }
                    .el-carousel__item:nth-child(2n) {
                        background-color: #99a9bf;
                    }
                    .el-carousel__item:nth-child(2n+1) {
                        background-color: #d3dce6;
                    }
                }
                .small-img {
                    display: inline-block;
                    width: 300px;
                    margin-right: 15px;
                    .small-one {
                        height: 145px;
                        overflow: hidden;
                        &.small-one:first-child {
                            margin-bottom: 10px;
                        }
                        img {
                            width: 100%;
                            height: 100%;
                            transition: all 0.6s;
                            opacity: 0.8;
                        }
                        img:hover {
                            opacity: 1;
                            transform: scale3d(1.2, 1.2, 5);
                        }
                    }
                }
            }
            .page-content {
                padding: 0 20px;
                .title {
                    border-bottom: 1px solid lightgrey;
                    h2 {
                        display: inline-block;
                        border-bottom: 2px solid black;
                        transition: all 1s;
                    }
                    h2:hover {
                        width: 100%;
                    }
                }
                .content {
                    display: flex;
                    align-items: center;
                    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
                    position: relative;
                    font-size: 16px;
                    margin: 20px;
                    transition: all 1s;
                    &.content:hover {
                        box-shadow: 0 0 15px 5px #888;
                        transform: scale(1.01, 1.01);
                        img {
                            transform: translate(600px, 0)
                        }
                        .text {
                            transform: translate(-200px, 0)
                        }
                    }
                    .blogs-text {
                        width: 70%;
                    }
                    img {
                        margin-right: 10px;
                        transition: all 1s;
                    }
                    .text {
                        transition: all 1s;
                        h1 {
                            padding-bottom: 2px;
                        }
                        h1:hover {
                            color: #3499ff;
                            border-bottom: 1px solid gold;
                        }
                        .tape {
                            position: absolute;
                            bottom: 10px;
                            width: 50px;
                            height: 30px;
                            color: greenyellow;
                            background: #f4fffc;
                            text-align: center;
                            line-height: 30px;
                            font-size: 20px;
                        }
                    }
                }
            }
            .page-sut {
                margin-top: 20px;
                padding: 0 20px;

                .title {
                    border-bottom: 1px solid lightgrey;
                    h2 {
                        display: inline-block;
                        border-bottom: 2px solid black;
                        transition: all 1s;
                    }
                    h2:hover {
                        width: 100%;
                    }
                }
                .text {
                    width: 40%;
                    display: inline-block;
                    margin: 20px 42px;
                    transition: all 1s;
                    h2{
                        color:rgb(7,17,27);
                    }
                    &.text:hover {
                        h2{
                            color: #3499ff;
                        }

                        .span {
                            .one {
                                transform: translate(180px, 0);
                            }
                            .two {
                                transform: translate(-150px, 0);
                            }
                        }
                    }
                    .span {
                        padding: 20px;
                        border: 1px solid lightgrey;
                        border-left: none;
                        border-right: none;
                        display: flex;
                        flex-direction: row;

                        div {
                            display: inline-block;
                            margin-right: 10px;
                            transition: all 1s;
                        }

                    }
                }
            }
        }
        .paga-right {
            width: 20%;
            margin-top: 50px;
        }
    }
</style>